import ti from './TableItem.module.css';

const TableItem = (props) => {
    const data = props.data;

    let nameFirstLetter = data.name.slice(0, 1) + '.';

    if (!data.tyres) data.tyres = 'нет данных';

    function toggleSpecs(event) {
        if (event.target.tagName !== 'LI') return;

        const list = event.target.children[2];

        event.target.classList.toggle(ti.altAfter);
        list.classList.toggle(ti.showList);
    }

    return (
        <li className={ti.tableItem} onClick={toggleSpecs}>
            <span className={ti.car}> {data.lastName} {nameFirstLetter} <br/> {data.car}</span>
            <span className={ti.time}> {data.time} </span>

            <div className={ti.dropdownSpecs}>
                <p className={ti.dropdownItem + ' flexRow'}> {data.carSpecs} </p>

                {/*<p className="dropdownItem flexRow">*/}
                {/*    <span className="dropdown__span">Событие:</span>*/}
                {/*    <span className="dropdown__span">{data.event}</span>*/}
                {/*</p>*/}

                {/*<p className="dropdownItem flexRow">*/}
                {/*    <span className="dropdown__span">Дата:</span>*/}
                {/*    <span className="dropdown__span">{data.date}</span>*/}
                {/*</p>*/}

                <p className={ti.dropdownItem + ' flexRow'}>
                    <span className={ti.dropdown__span}>Класс:</span>
                    <span className={ti.dropdown__span}>{data.class}</span>
                </p>

                <p className={ti.dropdownItem + ' flexRow'}>
                    <span className={ti.dropdown__span}>Резина:</span>
                    <span className={ti.dropdown__span}>{data.tyres}</span>
                </p>
            </div>

        </li>
    );
}

export default TableItem;