import menu from './Menu.module.css';

const Menu = () => {
    function toggleMenu() {
        const m = document.querySelector(`.${menu.menu}`);
        m.classList.toggle(menu.show);
        m.classList.toggle(menu.hide);
    }

    function toggleDropdown(event) {
        const list = event.target.nextElementSibling;

        event.target.classList.toggle(menu.altAfter);
        list.classList.toggle(menu.showList);
    }

    return (
            <nav>
                <a className={menu.menuButton} onClick={toggleMenu}>Меню</a>

                <ul className={menu.menu + ' ' +  menu.hide}>
                    <li>
                        <a className={menu.dropdown} onClick={toggleDropdown}>событие</a>
                        <ul className={menu.list} id="events">
                            <li className={menu.item}><a>BRD 2018/09/22</a></li>
                            <li className={menu.item}><a>BRD 2020/10/10</a></li>
                            <li className={menu.item}><a>Big Piston 2015/07/01</a></li>
                        </ul>
                    </li>

                    <li>
                        <a className={menu.dropdown} onClick={toggleDropdown}>Сортировать</a>
                        <ul className={menu.list} id="sortTable">
                            <li className={menu.item}><a>По времени, в начале быстрые</a></li>
                            <li className={menu.item}><a>По времени, в начале медленные</a></li>
                            <li className={menu.item}>
                                <a onClick={toggleDropdown}>По классам</a>
                                <ul className={menu.list}>
                                    <li className={menu.item}>S1500</li>
                                    <li className={menu.item}>S2000</li>
                                    <li className={menu.item}>S3000</li>
                                    <li className={menu.item}>Unlim</li>
                                </ul>
                            </li>
                        </ul>
                    </li>

                    <li>
                        <a className={menu.dropdown} onClick={toggleDropdown}>Еще что-то</a>
                        <ul className={menu.list} id="a">
                            <li className={menu.item}><a>item</a></li>
                            <li className={menu.item}><a>item</a></li>
                            <li className={menu.item}><a>item</a></li>
                        </ul>
                    </li>
                </ul>

            </nav>
    )
};

export default Menu;