import React, {useState, useEffect} from 'react';
import {
    BrowserRouter as Router,
    Switch, Route, Link
} from 'react-router-dom';

import './App.css';
import TableItem from "./components/TableItem";
import table1 from './components/brd_2020_08_29.json';
import Menu from './components/Menu';

function App() {
    const eventAndDate = table1[0]['date'] + ' ' + table1[0]['event'];

    let [table, setTable] = useState(table1);

    const sortByTime = () => {
        let dataset = document.querySelector('[data-sort]').dataset;
        let tempVar;
        let tempArr = [...table];

        if (dataset.sort === 'desc' || dataset.sort === '') {
            dataset.sort = 'asc';

            for (let i = 0; i < tempArr.length; i++) {
                for (let j = i + 1; j < tempArr.length; j++) {
                    if (tempArr[j]['time'] < tempArr[i]['time']) {
                        tempVar = tempArr[i];
                        tempArr[i] = tempArr[j];
                        tempArr[j] = tempVar;
                    }
                }
            }

            setTable(tempArr);
        } else {
            dataset.sort = 'desc';

            for (let i = 0; i < tempArr.length; i++) {
                for (let j = i + 1; j < tempArr.length; j++) {
                    if (tempArr[j]['time'] > tempArr[i]['time']) {
                        tempVar = tempArr[j];
                        tempArr[j] = tempArr[i];
                        tempArr[i] = tempVar;
                    }
                }
            }

            setTable(tempArr);
        }
    };

    return (
        // <div className="App">
        //     <header className="header"><h1>laptimes-ua</h1></header>
        //
        //     <main>
        //         <div className="table">
        //             <div className="tableHeader">
        //                 <h4 className="table__title">{eventAndDate}</h4>
        //                 <span>Аутомобиль</span>
        //                 <span className="tableHeader__timeHeader"
        //                       data-sort=""
        //                       onClick={sortByTime}>&uarr;&darr;время</span>
        //             </div>
        //
        //             <ul>
        //                 {table.map((elem) => <TableItem data={elem}/>)}
        //             </ul>
        //         </div>
        //         <hr/>
        //
        //         <Menu/>
        //
        //     </main>
        //
        //     <footer>
        //     </footer>
        //
        // </div>

        <Router>
            <div>
                <Link to="/table">table</Link>
            </div>

            <Switch>
                <Route path="/table">
                         <header className="header"><h1>laptimes-ua</h1></header>

                         <main>
                             <div className="table">
                                 <div className="tableHeader">
                                     <h4 className="table__title">{eventAndDate}</h4>
                                     <span>Аутомобиль</span>
                                     <span className="tableHeader__timeHeader"
                                           data-sort=""
                                          onClick={sortByTime}>&uarr;&darr;время</span>
                                </div>

                                <ul>
                                    {table.map((elem) => <TableItem data={elem}/>)}
                                </ul>
                            </div>
                            <hr/>

                            <Menu/>
                         </main>
                </Route>
            </Switch>
        </Router>
    );
}

export default App;
